<script setup>
import { ref, onMounted, watch } from 'vue'
import { useAudioStore } from '@/store/audio.js'

const audioRef = ref(null)
const audioStore = useAudioStore()

onMounted(() => {
  audioStore.audio = audioRef.value
})

const canplay = () => {
  audioStore.totalTime = audioStore.formatTime(audioRef.value.duration)
  console.log('可以播放')
}

// 监听audioPath的变化，更新audio实例
watch(
  () => audioStore.audioPath,
  () => {
    // 在audioPath变化时重新获取audio实例
    audioStore.audio = audioRef.value
  }
)
</script>

<template>
  <!-- 放在这里解决路由跳转刷新的问题 -->
  <audio
    preload="auto"
    controls
    ref="audioRef"
    @canplay="canplay"
    @timeupdate="audioStore.timeChange"
  >
    <source :src="audioStore.audioPath" />
  </audio>

  <router-view v-slot="{ Component }">
    <transition name="fade" mode="out-in">
      <keep-alive>
        <component :is="Component" />
      </keep-alive>
    </transition>
  </router-view>
</template>

<style scoped>
a {
  padding: 20px;
}

.fade-enter-from,
.fade-leave-to {
  /* 定义进入开始和离开结束的透明度为 0 */
  opacity: 0;
}

.fade-leave-to {
  transform: translatex(20px);
}

.fade-enter-to {
  transform: translatex(-20px);
}

.fade-enter-to,
.fade-leave-from {
  /* 定义进入结束和离开开始的透明度为 1 */
  opacity: 1;
}

.fade-leave-active,
.fade-enter-active {
  transition: all 0.3s ease-out;
}
.fade-enter-to {
  transform: translatey(0);
  opacity: 1;
  transition: all 0.3s ease-out 0.2s;
}
</style>
